import React from 'react';
import { Box, Typography, Rating } from '@mui/material';
import StarIcon from '@mui/icons-material/Star';

interface ReviewCardProps {
  name: string;
  comment: string;
  rating: number;
  university: string;
  profileImage?: string;
  hasVideo?: boolean;
  onPlayClick?: () => void;
}

const ReviewCard: React.FC<ReviewCardProps> = ({
  name,
  comment,
  rating,
  university,
  profileImage,
  hasVideo = false,
  onPlayClick
}) => {
  return (
    <Box
      sx={{
        position: 'relative',
        width: '100%',
        maxWidth: 520,
        background: '#fff',
        borderRadius: '24px',
        boxShadow: '0 2px 16px 0 rgba(60,72,100,0.08)',
        p: { xs: 3, md: 4 },
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        minHeight: 220,
        cursor: hasVideo ? 'pointer' : 'default',
        transition: 'all 0.3s ease',
        '&:hover': hasVideo ? {
          transform: 'translateY(-4px)',
          boxShadow: '0 4px 20px 0 rgba(60,72,100,0.15)'
        } : {}
      }}
      onClick={hasVideo ? onPlayClick : undefined}
    >
      {/* Profile Image Container - Hidden for written testimonials */}
      {false && (
        <Box
          sx={{
            position: 'absolute',
            top: -48,
            left: '50%',
            transform: 'translateX(-50%)',
            width: 96,
            height: 96,
            background: '#FFF6F0',
            borderRadius: '50%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            boxShadow: '0 2px 8px 0 rgba(60,72,100,0.06)',
          }}
        >
          <img
            src={profileImage || 'https://via.placeholder.com/96'}
            alt={name}
            style={{ 
              width: 80, 
              height: 80, 
              borderRadius: '50%', 
              objectFit: 'cover', 
              border: '3px solid #fff' 
            }}
          />
        </Box>
      )}

      {/* Content */}
      <Box sx={{ mt: 3, width: '100%', textAlign: 'center' }}>
        {/* Name */}
        <Typography 
          fontWeight={600} 
          fontSize="1.5rem" 
          sx={{ mb: 1, color: '#2C3E50' }}
        >
          {name}
        </Typography>

        {/* University */}
        <Typography 
          color="#70A6B2" 
          fontSize="0.9rem" 
          sx={{ mb: 2, fontWeight: 500 }}
        >
          {university}
        </Typography>

        {/* Rating */}
        <Box sx={{ mb: 2, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Rating
            value={rating}
            readOnly
            precision={0.5}
            size="small"
            icon={<StarIcon sx={{ color: '#FFD700' }} />}
            emptyIcon={<StarIcon sx={{ color: '#E0E0E0' }} />}
          />
        </Box>

        {/* Comment */}
        <Typography 
          color="#555" 
          fontSize="1rem" 
          sx={{ 
            fontWeight: 400, 
            lineHeight: 1.6, 
            textAlign: 'center',
            fontStyle: 'italic',
            position: 'relative',
            '&::before': {
              content: '"66"',
              position: 'absolute',
              top: -30,
              left: -10,
              fontSize: '2rem',
              color: '#70A6B2',
              opacity: 0.3,
              fontFamily: 'serif',
              lineHeight: 1
            }
          }}
        >
          {comment}
        </Typography>

        {/* Video Play Button (if applicable) */}
        {hasVideo && (
          <Box
            sx={{
              position: 'absolute',
              bottom: 16,
              right: 16,
              width: 40,
              height: 40,
              background: 'rgba(0,0,0,0.7)',
              borderRadius: '50%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              cursor: 'pointer',
              transition: 'all 0.3s ease',
              '&:hover': {
                background: 'rgba(0,0,0,0.9)',
                transform: 'scale(1.1)'
              }
            }}
            onClick={(e) => {
              e.stopPropagation();
              onPlayClick?.();
            }}
          >
            <Box
              sx={{
                width: 0,
                height: 0,
                borderLeft: '12px solid white',
                borderTop: '8px solid transparent',
                borderBottom: '8px solid transparent',
                marginLeft: '2px'
              }}
            />
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default ReviewCard;
