import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../store';

// Base selectors
export const selectUserAuth = (state: RootState) => state.userAuthStatus;
export const selectSharedInfo = (state: RootState) => state.sharedInfo;
export const selectRecommendations = (state: RootState) => state.recommendations;
export const selectShortlistedCourses = (state: RootState) => state.shortlistedcourses;
export const selectApplications = (state: RootState) => state.applications;
export const selectPreferences = (state: RootState) => state.preferences;
export const selectPopup = (state: RootState) => state.popup;

// Memoized selectors for better performance
export const selectIsAuthenticated = createSelector(
  [selectUserAuth],
  (auth) => auth.data?.isAuthorized || false
);

export const selectUserProfile = createSelector(
  [selectSharedInfo],
  (sharedInfo) => sharedInfo.data
);

export const selectFilteredRecommendations = createSelector(
  [selectRecommendations, (state: RootState, filter: string) => filter],
  (recommendations, filter) => {
    if (!recommendations.data?.data || !Array.isArray(recommendations.data.data)) {
      return [];
    }
    return recommendations.data.data.filter((item: any) => 
      item.possibilityOfAdmit === filter
    );
  }
);

export const selectAmbitiousRecommendations = createSelector(
  [selectRecommendations],
  (recommendations) => {
    if (!recommendations.data?.data || !Array.isArray(recommendations.data.data)) {
      return [];
    }
    return recommendations.data.data.filter((item: any) => 
      item.possibilityOfAdmit === "Ambitious"
    );
  }
);

export const selectModerateRecommendations = createSelector(
  [selectRecommendations],
  (recommendations) => {
    if (!recommendations.data?.data || !Array.isArray(recommendations.data.data)) {
      return [];
    }
    return recommendations.data.data.filter((item: any) => 
      item.possibilityOfAdmit === "Moderate"
    );
  }
);

export const selectSafeRecommendations = createSelector(
  [selectRecommendations],
  (recommendations) => {
    if (!recommendations.data?.data || !Array.isArray(recommendations.data.data)) {
      return [];
    }
    return recommendations.data.data.filter((item: any) => 
      item.possibilityOfAdmit === "Safe"
    );
  }
);

export const selectShortlistedCourseIds = createSelector(
  [selectShortlistedCourses],
  (shortlisted) => shortlisted.data?.map((course: any) => course._id) || []
);

export const selectIsCourseShortlisted = createSelector(
  [selectShortlistedCourseIds, (state: RootState, courseId: string) => courseId],
  (shortlistedIds, courseId) => shortlistedIds.includes(courseId)
);

export const selectUserCurrency = createSelector(
  [selectPreferences],
  (preferences) => preferences.data?.currency || 'INR'
);

export const selectPopupData = createSelector(
  [selectPopup],
  (popup) => popup.data
);

// Performance optimized selectors for large lists
export const selectCoursesWithShortlistStatus = createSelector(
  [selectShortlistedCourseIds, (state: RootState, courses: any[]) => courses],
  (shortlistedIds, courses) => {
    if (!Array.isArray(courses)) return [];
    return courses.map(course => ({
      ...course,
      isShortlisted: shortlistedIds.includes(course._id || course.id)
    }));
  }
);